body{
    background-image: url("img/final.jpg");
    background-repeat: no-repeat;
    background-origin: content-box;
    color: #050709;
}
header {
    text-align: center;
    background-color: #f8f8f8;
    padding: 0.5em;
    font-family: Helvetica, sans-serif;
    -webkit-box-shadow: 0px 10px 22px -14px rgba(5,7,9,1);
    -moz-box-shadow: 0px 10px 22px -14px rgba(5,7,9,1);
    box-shadow: 0px 10px 22px -14px rgba(5,7,9,1);
}

h1{
    text-transform: uppercase;
}

header p{
    font-style: italic;
    padding: 0.1em;
}

header p a{
    text-decoration: none;
    color:  #3455DB;
}
.estimator{
    display: flex;
    flex-flow: column nowrap;
    justify-content: center;
    align-items: center;
    padding: 0.5em;
    background: #3556DB;
    background: -webkit-linear-gradient(top right, #3556DB, #2675A9);
    background: -moz-linear-gradient(top right, #3556DB, #2675A9);
    background: linear-gradient(to bottom left, #3556DB, #2675A9);
    width: 300px;
    margin: auto;
    margin-top: 1em;
    border-radius: 10px;
    -webkit-box-shadow: 0px 0px 109px -16px rgb(44, 62, 80);
    -moz-box-shadow: 0px 0px 109px -16px rgb(44, 62, 80);
    box-shadow: 0px 0px 109px -16px rgb(44, 62, 80);
}
.estimator label{
    text-align: center;
    color: #f4f4f4;
}
.estimator svg{
    margin: 0.12em;
    color: #f4f4f4;
}

.estimator input{
    padding: 0.5em;
    width: 90%;
    border-radius: 3px;
    border-style: none;
}

.estimator button{
    margin: 0.5em;
    padding: 0.8em;
    background-color: #0A3055;
    color: #f8f8f8;
    border-radius: 5px;
    border: none;
    transition: all 0.5s ease;
}

.estimator button:hover{
    background-color: #050709;
    border: 2px inset rgba(10, 65, 155, 0.15);
    padding: 0.9em;
}

.estimator select{
    background-color: #205D86;
    color: #f8f8f8;
    padding: 0.3em;
    border-radius: 3px;
}

.estimator ul li{
    list-style-type: lower-roman;
    color: #f8f8f8;
}
footer {
    text-align: center;
    background-color: #cfcfce;
    margin-top: 1em;
    padding: 0.01em;
}